:root {
    --text-color: #c5dce6;
    --background-light: #b6ccd1;
    --navbar-light: #768487;
    --background-dark: #0c3345;
    --navbar-dark: #071c26;
    --nav-size: 60px;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
    --speed: 500ms;
}
.App {
    margin: 0;
}
/*/ Theme's for background, add .component to end for indiv use /*/
#light {
    background-color: var(--background-light);
}
#dark {
    background-color: var(--background-dark);
}

body {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: var(--text-color);
    text-decoration: none;
}

.navbar {
    height: var(--nav-size);
    padding: 0 1 rem;
    border-bottom: var(--border);
}
.navbar-nav {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}
.nav-item {
    width: calc(var(--nav-size) * 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
}
.icon-button {
    --button-size: calc(var(--nav-size) * 0.5);
    width: var(--button-size);
    height: var(--button-size);
    border-radius: 50%;
    background-color: #393f40;
    padding: 5px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: filter 300ms;
}
.icon-button:hover {
    filter: brightness(1.2);
}
#light .navbar {
    background-color: var(--navbar-light);
}

#dark .navbar {
    background-color: var(--navbar-dark);
}
